html {
  font-family: "Open Sans", sans-serif;
  font-size: 16px; }

body,
ul,
ol {
  margin: 0;
  padding: 0; }

body {
  position: relative; }

*,
*::before,
*::after {
  box-sizing: border-box;
  font-variant-numeric: lining-nums; }

fieldset {
  border: none; }

input::-moz-placeholder {
  opacity: 1; }

input::-webkit-input-placeholder {
  opacity: 1; }

input:-ms-input-placeholder {
  opacity: 1; }

input::placeholder {
  opacity: 1; }

input[type='number'] {
  -moz-appearance: textfield; }
  input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

select::-ms-expand {
  display: none; }

h1,
p {
  margin-top: 0; }

@font-face {
  font-family: 'Open Sans';
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../assets/fonts/subset-OpenSans-Bold.woff2") format("woff2"), url("../assets/fonts/subset-OpenSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'Open Sans';
  src: local("Open Sans ExtraBold"), local("OpenSans-ExtraBold"), url("../assets/fonts/subset-OpenSans-ExtraBold.woff2") format("woff2"), url("../assets/fonts/subset-OpenSans-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap; }

.container {
  max-width: 1371px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 30px;
  padding-left: 30px; }
  .container_wide {
    max-width: 1561px; }

@media screen and (max-width: 443px) {
  .container {
    padding-right: 15px;
    padding-left: 15px; } }

.logo {
  display: inline-block;
  text-decoration: none; }
  .logo__img {
    width: 263px;
    height: 78px; }

.header {
  padding-top: 45px;
  padding-bottom: 37px; }

.projects-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none; }
  .projects-list__item {
    flex-basis: calc((100% - 34px * 2) / 3);
    margin-right: 34px;
    margin-bottom: 50px; }
    .projects-list__item:nth-child(3n) {
      margin-right: 0; }

@media screen and (max-width: 1370px) {
  .projects-list {
    max-width: 862px;
    margin-right: auto;
    margin-left: auto; }
    .projects-list__item {
      flex-basis: calc((100% - 34px) / 2); }
      .projects-list__item:nth-child(3n) {
        margin-right: 34px; }
      .projects-list__item:nth-child(2n) {
        margin-right: 0; } }

@media screen and (max-width: 921px) {
  .projects-list {
    max-width: 414px; }
    .projects-list__item {
      flex-basis: 100%;
      max-width: 638px; }
      .projects-list__item:nth-child(n) {
        margin-right: 0; } }

.project-card {
  display: block;
  position: relative;
  padding-top: 100%;
  border-radius: 10px;
  box-shadow: 0 14px 38px 0 rgba(0, 0, 0, 0.5);
  overflow: hidden;
  text-decoration: none; }
  .project-card__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 50px 35px 32px; }
  .project-card .project-card-name {
    flex-shrink: 0;
    margin-bottom: 15px; }
  .project-card .project-card-img {
    flex-grow: 1;
    max-width: 90%; }
  .project-card__overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #000000;
    opacity: 0;
    transition: opacity 0.3; }
  .project-card .button {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .project-card:hover .project-card__overlay, .project-card:focus .project-card__overlay, .project-card:active .project-card__overlay {
    opacity: 0.5; }
  .project-card:hover .button, .project-card:focus .button, .project-card:active .button {
    display: flex; }
  .project-card_geo {
    background-color: #69a6e3; }
  .project-card_sadko {
    background-color: #537699; }
  .project-card_garbage {
    background-color: #152049; }
  .project-card_celsus {
    background-color: #3e4972; }
  .project-card_overhaul {
    background-color: #83b1de; }
  .project-card_building {
    background-color: #1d5c9c; }

.project-card-name {
  max-width: 100%;
  color: #ffffff;
  font-size: 24px;
  font-weight: 700;
  text-align: center; }

@media screen and (max-width: 443px) {
  .project-card-name {
    font-size: 16px; } }

.project-card-img {
  position: relative;
  width: 100%; }
  .project-card-img__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain; }

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 57px;
  padding-top: 12px;
  padding-right: 60px;
  padding-bottom: 12px;
  padding-left: 60px;
  border: none;
  background-color: #e06061;
  border-radius: 28px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 800;
  text-align: center;
  cursor: pointer; }

@media screen and (max-width: 443px) {
  .button {
    min-height: 48px;
    font-size: 16px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  bottom: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.overflow_hidden {
  overflow: hidden; }

.overflow-y_auto {
  overflow-y: auto; }

.overflow-y_visible {
  overflow-y: visible; }

.overflow-y_hidden {
  overflow-y: hidden; }

.swiper-wrapper > * {
  flex-shrink: 0; }
